import React, { useContext } from 'react';
import './Card.component.scss';
import variables from '../../styles/_variables.module.scss';
import { AppContext } from '../../AppContext';

export default function Card({ id, icon, title }) {
  const { themeColorObj } = useContext(AppContext);
  const themeColor = themeColorObj[0];

  return (
    <div
      key={id}
      className="card"
      style={
        themeColor === 'light'
          ? { backgroundColor: variables.cardBgLight }
          : { backgroundColor: variables.cardBgDark }
      }
    >
      <div className="card-image-socket">
        <img src={icon} alt="icon" />
      </div>
      <div className="paragraph-socket">
        <p>{title}</p>
      </div>
    </div>
  );
}
